import focusPlayer from "./focusPlayer";

let timer = null;
export default {
	props: ["list"],
	components: {
		focusPlayer
	},
	data() {
		return {
			index: 0
		};
	},
	methods:{
		// 一般又scroll函数，一般都是要设置消抖的，提高代码性能, 这是消抖，应该学习用法
		scroll(res) {
			clearTimeout(timer);
			timer = setTimeout(() => {
				const index = Math.floor((res.detail.scrollTop + 60) / 550);
				this.index = index;
			}, 100);
		}
	},
	watch: {
		// 当有核心变量的改变时，使用watch监听data中的变量的变化
		// 这种做法真的很巧妙，值得进行记忆(记录笔记)
		index() {
			if(this.index >= 0 && this.index < this.list.length) {
				this.$refs.player[this.index].play();
			}
			if((this.index-1) >= 0 && (this.index-1) < this.list.length) {
				this.$refs.player[this.index - 1].pause();
			}
			if((this.index + 1) >= 0 && (this.index + 1) < this.list.length) {
				this.$refs.player[this.index + 1].pause();
			}
		}
	},
}
